import { useMutation, useQueryClient } from "@tanstack/react-query";
import { updateLeadDetailsApi } from "@/lib/mock-api";

const DEFAULT_TENANT = "tenant-demo-1";

export function useUpdateLeadDetails(tenantId: string = DEFAULT_TENANT) {
  const client = useQueryClient();
  return useMutation({
    mutationFn: (payload: {
      leadId: string;
      email?: string;
      phone?: string;
      name?: string;
    }) => updateLeadDetailsApi(tenantId, payload.leadId, payload),
    onSuccess: (lead) => {
      client.invalidateQueries({ queryKey: ["leads", tenantId] });
      client.invalidateQueries({ queryKey: ["lead", tenantId, lead.id] });
    },
  });
}

